ul {
    padding: 0;
    margin: 0;
}

h2, p {
    margin: 0;
}

button:focus {
    outline: none;
}

.flex-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.nav ul {
    display: -ms-inline-flexbox;
    display: inline-flex;
    list-style: none;
    -ms-flex-pack: center;
        justify-content: center;
}

/****************************Fly buttons custom content*******************************/
.slow:before {
    content: url('./images/fly-green-sm.png');
}

.normal:before {
    content: url('./images/fly-blue-sm.png');
}

.fast:before {
    content: url('./images/fly-red-sm.png');
}

.slow:after {
    content: '+50';
}

.normal:after {
    content: '+75';
}

.fast:after {
    content: '+100';
}

/********************************Start button animation******************************/
@-webkit-keyframes pulse {
    0% {

        -webkit-box-shadow: 0 0 0 0;

                box-shadow: 0 0 0 0;
    }
    100% {
        -webkit-box-shadow: 0 0 50px 15px;
                box-shadow: 0 0 50px 15px;
    }
}
@keyframes pulse {
    0% {

        -webkit-box-shadow: 0 0 0 0;

                box-shadow: 0 0 0 0;
    }
    100% {
        -webkit-box-shadow: 0 0 50px 15px;
                box-shadow: 0 0 50px 15px;
    }
}

.start {
    -webkit-animation: pulse infinite 1s alternate linear;
            animation: pulse infinite 1s alternate linear;
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}

.start.pulse-animation {
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

/**************************************LAYOUT***************************************/

.general-container {
    display: grid;
    grid-template-areas: 'header' 'progress' 'main';
    grid-template-rows: auto;
}

.header {
    grid-area: header;
    background-color: #222;
    color: white;
    padding: 10px 0;
    text-align: center;
}

.timer {
    grid-area: progress;
}

.content {
    grid-area: main;

}

.jar-container {
    text-align: center;
}

/*.score-container {*/
